table,
tr,
td,
th {
  font-size: 18px;
}

.table th,
.table td {
  padding: 0.5rem;
}

#week-select select {
  float: right;
  margin-bottom: -30px;
  margin-top: 25px;
  z-index: 9999;
  position: relative;
}

section.week table {
  margin-bottom: 60px;
}

.count_box {
  // position: fixed;
  // bottom: 0;
  // left: 0;
  // width: 100%;
  // height: 45px;
  // background: #104109;
  // display: flex;
  // flex-direction: row;
  // align-items: center;
  // margin: 0;
  position: fixed;
  bottom: 45px;
  right: 45px;
  background: #fff;
  padding: 25px;
  box-shadow: 3px 6px 38px #dadada;
  z-index: 9999;

  ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
  }
}

.count_box h4,
.count_box li {
  font-size: 19px;
}

.count_box h4 {
  margin: 0 0 1rem 0;
  padding: 0;
  color: $bright-green;
}

.count_box ul li {
  margin: 0;
  padding: 0;
}

table.footable > tbody > tr > td,
table.footable > tbody > tr > th {
  display: table-cell;
}



